.warningProcess-content{width:90%;height:80%;background:#f2f2f2;position: absolute;top:0px;right: 0px;bottom: 0px;left: 0px;margin: auto
}
#warningPanelSetting{width:350px;background:white;border-radius:5px;font-size:12px;position:absolute;-webkit-box-shadow:0px 3px 5px 0px rgba(0,49,98,0.08);box-shadow:0px 3px 5px 0px rgba(0,49,98,0.08);top: 0px;right: 0px;bottom: 0px;left: 0px;margin: auto;height: 270px;
}
