#warningPanelSetting .topline{background:#5ba7ff;height: 5px;border-top-left-radius: 5px;border-top-right-radius: 5px;
}
#warningPanelSetting .common-head{height: 45px;line-height: 45px;position: relative;font-size: 14px;
}
#warningPanelSetting .common-head .close{position: absolute;right: 10px;height: 100%;width: 45px;top: 10px;text-align: right;cursor: pointer;
}
#warningPanelSetting .common-head .logo{margin-left: 10px;font-size:14px;font-weight: bold;
}
