}
#warningPanelSetting .common-filter-footer a{width: 70px;height: 25px;position: absolute;border-radius: 2px;line-height: 25px;text-align: center;-webkit-box-sizing: border-box;box-sizing: border-box;
}
#warningPanelSetting .common-filter-footer .cancleBtn{top: 10px;right: 110px;border: 1px solid #DEDEDE;color: #000;
}
#warningPanelSetting .common-filter-footer .confirmBtn{background: #5ba7ff;color: white;right: 20px;top: 10px;border: 1px solid #3190ff;
}
