}
#warningPanelSetting .warning-body .warning-setting .warning-setting-area table tr td:nth-child(1){width:120px;
}
#warningPanelSetting .warning-body .warning-setting .warning-setting-area table tr td:nth-child(2){width:50px;
}
#warningPanelSetting .warning-body .warning-setting .warning-setting-area table tr td:nth-child(3){width:90px;
}
#warningPanelSetting .warning-body .warning-setting .warning-setting-area table tr td:nth-child(4){width:70px;
}
#warningPanelSetting .warning-body .warning-setting .warning-setting-area table tr td:nth-child(5){width:20px;border-right: none;
}
